import React, { FC } from "react";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import { MuiFileInput } from "mui-file-input";
import { StyledBoxforModal } from "./ModalStyles";

interface IEditPhotoModal {
  openModal: boolean;
  savePhoto: (file: any) => void;
  handleCloseModal: () => void;
}

const EditPhotoModal: FC<IEditPhotoModal> = ({ openModal, savePhoto, handleCloseModal }) => {
  const [file, setFile] = React.useState(null);

  const handleChange = (newFile: any) => {
    setFile(newFile);
    savePhoto(newFile);
    setTimeout(() => {
      handleCloseModal();
    }, 2000);
  };
  return (
    <div>
      <Modal open={openModal} onClose={handleCloseModal}>
        <StyledBoxforModal>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Ð—Ð°Ð³Ñ€ÑƒÐ·ÐºÐ° Ð½Ð¾Ð²Ð¾Ð¹ Ñ„Ð¾Ñ‚Ð¾Ð³Ñ€Ð°Ñ„Ð¸Ð¸
          </Typography>
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            Ð’Ñ‹ Ð¼Ð¾Ð¶ÐµÑ‚Ðµ Ð·Ð°Ð³Ñ€ÑƒÐ·Ð¸Ñ‚ÑŒ Ð¸Ð·Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð¸Ðµ Ð² Ñ„Ð¾Ñ€Ð¼Ð°Ñ‚Ðµ jpg Ð¸Ð»Ð¸ png.
          </Typography>
          <MuiFileInput sx={{ mt: 2 }} value={file} onChange={handleChange} />
        </StyledBoxforModal>
      </Modal>
    </div>
  );
};

export default EditPhotoModal;
